.fox_backgroud_slider{
  * img{
    object-fit: cover !important;
  }
  .slider_wrapper{
    .slider_inside{
      width: 100%;
      height: 100%;
      img{
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        transition: all 8s;
        opacity: 0;
      }
    }
  }
}